<template>
  <div class="geometry-game">
    <div class="game-header">
      <h2>üìê Âá†‰ΩïÊé¢Á¥¢</h2>
      <div class="game-stats">
        <span class="score">ÂæóÂàÜ: {{ score }}</span>
        <span class="time">Êó∂Èó¥: {{ timeLeft }}s</span>
        <span class="level">Á≠âÁ∫ß: {{ level }}</span>
      </div>
    </div>

    <div v-if="!gameStarted" class="game-start">
      <div class="start-content">
        <h3>ÂáÜÂ§áÂºÄÂßãÂá†‰ΩïÊé¢Á¥¢ÔºÅ</h3>
        <p>Êé¢Á¥¢Âá†‰Ωï‰∏ñÁïåÔºåËÆ§ËØÜÂêÑÁßçÂõæÂΩ¢ÔºåËÆ°ÁÆóÈù¢ÁßØÂíåÂë®Èïø</p>
        <div class="difficulty-select">
          <label>ÈÄâÊã©ÈöæÂ∫¶:</label>
          <select v-model="selectedDifficulty">
            <option value="easy">ÁÆÄÂçï (Âü∫Êú¨ÂõæÂΩ¢)</option>
            <option value="medium">‰∏≠Á≠â (Èù¢ÁßØËÆ°ÁÆó)</option>
            <option value="hard">Âõ∞Èöæ (Âë®ÈïøËÆ°ÁÆó)</option>
          </select>
        </div>
        <button class="btn btn-primary" @click="startGame">ÂºÄÂßãÊ∏∏Êàè</button>
      </div>
    </div>

    <div v-else-if="!gameOver" class="game-play">
      <div class="question-container">
        <div class="question">
          <div v-if="gameType === 'identify'" class="identify-shape">
            <div class="shape-display">
              <div class="shape" :class="currentShape.type">
                <div v-if="currentShape.type === 'circle'" class="circle"></div>
                <div v-else-if="currentShape.type === 'square'" class="square"></div>
                <div v-else-if="currentShape.type === 'rectangle'" class="rectangle"></div>
                <div v-else-if="currentShape.type === 'triangle'" class="triangle"></div>
              </div>
            </div>
            <p class="question-text">ËøôÊòØ‰ªÄ‰πàÂõæÂΩ¢Ôºü</p>
            <div class="shape-options">
              <button 
                v-for="option in shapeOptions" 
                :key="option"
                class="shape-option"
                :class="{ 'selected': userAnswer === option }"
                @click="userAnswer = option"
              >
                {{ option }}
              </button>
            </div>
          </div>
          
          <div v-else-if="gameType === 'area'" class="area-calculation">
            <div class="shape-display">
              <div class="shape" :class="currentShape.type">
                <div v-if="currentShape.type === 'circle'" class="circle"></div>
                <div v-else-if="currentShape.type === 'square'" class="square"></div>
                <div v-else-if="currentShape.type === 'rectangle'" class="rectangle"></div>
                <div v-else-if="currentShape.type === 'triangle'" class="triangle"></div>
              </div>
            </div>
            <div class="shape-info">
              <p v-if="currentShape.type === 'circle'">
                ÂúÜÁöÑÂçäÂæÑ: {{ currentShape.radius }} Âçï‰Ωç
              </p>
              <p v-else-if="currentShape.type === 'square'">
                Ê≠£ÊñπÂΩ¢ÁöÑËæπÈïø: {{ currentShape.side }} Âçï‰Ωç
              </p>
              <p v-else-if="currentShape.type === 'rectangle'">
                ÈïøÊñπÂΩ¢ÁöÑÈïø: {{ currentShape.length }} Âçï‰ΩçÔºåÂÆΩ: {{ currentShape.width }} Âçï‰Ωç
              </p>
              <p v-else-if="currentShape.type === 'triangle'">
                ‰∏âËßíÂΩ¢ÁöÑÂ∫ï: {{ currentShape.base }} Âçï‰ΩçÔºåÈ´ò: {{ currentShape.height }} Âçï‰Ωç
              </p>
            </div>
            <p class="question-text">ËÆ°ÁÆóËøô‰∏™ÂõæÂΩ¢ÁöÑÈù¢ÁßØ</p>
            <div class="answer-input">
              <input 
                v-model="userAnswer" 
                type="number" 
                placeholder="ËæìÂÖ•Èù¢ÁßØ"
                @keyup.enter="checkAnswer"
                ref="answerInput"
                autofocus
              />
              <span class="unit">Âπ≥ÊñπÂçï‰Ωç</span>
            </div>
          </div>
          
          <div v-else class="perimeter-calculation">
            <div class="shape-display">
              <div class="shape" :class="currentShape.type">
                <div v-if="currentShape.type === 'circle'" class="circle"></div>
                <div v-else-if="currentShape.type === 'square'" class="square"></div>
                <div v-else-if="currentShape.type === 'rectangle'" class="rectangle"></div>
                <div v-else-if="currentShape.type === 'triangle'" class="triangle"></div>
              </div>
            </div>
            <div class="shape-info">
              <p v-if="currentShape.type === 'circle'">
                ÂúÜÁöÑÂçäÂæÑ: {{ currentShape.radius }} Âçï‰Ωç
              </p>
              <p v-else-if="currentShape.type === 'square'">
                Ê≠£ÊñπÂΩ¢ÁöÑËæπÈïø: {{ currentShape.side }} Âçï‰Ωç
              </p>
              <p v-else-if="currentShape.type === 'rectangle'">
                ÈïøÊñπÂΩ¢ÁöÑÈïø: {{ currentShape.length }} Âçï‰ΩçÔºåÂÆΩ: {{ currentShape.width }} Âçï‰Ωç
              </p>
              <p v-else-if="currentShape.type === 'triangle'">
                ‰∏âËßíÂΩ¢ÁöÑ‰∏âËæπ: {{ currentShape.side1 }}, {{ currentShape.side2 }}, {{ currentShape.side3 }} Âçï‰Ωç
              </p>
            </div>
            <p class="question-text">ËÆ°ÁÆóËøô‰∏™ÂõæÂΩ¢ÁöÑÂë®Èïø</p>
            <div class="answer-input">
              <input 
                v-model="userAnswer" 
                type="number" 
                placeholder="ËæìÂÖ•Âë®Èïø"
                @keyup.enter="checkAnswer"
                ref="answerInput"
                autofocus
              />
              <span class="unit">Âçï‰Ωç</span>
            </div>
          </div>
        </div>
        
        <button class="btn btn-primary" @click="checkAnswer">Á°ÆËÆ§</button>
      </div>

      <div class="progress-bar">
        <div class="progress" :style="{ width: progressPercent + '%' }"></div>
      </div>

      <div class="feedback" v-if="showFeedback">
        <div :class="['feedback-message', isCorrect ? 'correct' : 'incorrect']">
          {{ feedbackMessage }}
        </div>
      </div>

      <div class="hint" v-if="showHint">
        <p>üí° ÊèêÁ§∫: {{ hintMessage }}</p>
      </div>
    </div>

    <div v-else class="game-over">
      <div class="game-over-content">
        <h3>Ê∏∏ÊàèÁªìÊùüÔºÅ</h3>
        <div class="final-stats">
          <p>ÊúÄÁªàÂæóÂàÜ: <span class="final-score">{{ score }}</span></p>
          <p>Ê≠£Á°ÆÈ¢òÁõÆ: {{ correctAnswers }}/{{ totalQuestions }}</p>
          <p>Áî®Êó∂: {{ totalTime }}Áßí</p>
        </div>
        <div class="game-actions">
          <button class="btn btn-primary" @click="restartGame">ÂÜçÁé©‰∏ÄÊ¨°</button>
          <button class="btn btn-secondary" @click="goBack">ËøîÂõûÊ∏∏ÊàèÂàóË°®</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, computed, onMounted, onUnmounted } from 'vue'
import { useRouter } from 'vue-router'
import { gameStorage } from '@/services/gameStorage'
import { achievementService } from '@/services/achievementService'

const router = useRouter()

// Ê∏∏ÊàèÁä∂ÊÄÅ
const gameStarted = ref(false)
const gameOver = ref(false)
const selectedDifficulty = ref('medium')
const score = ref(0)
const timeLeft = ref(60)
const level = ref(1)
const correctAnswers = ref(0)
const totalQuestions = ref(0)
const totalTime = ref(0)

// Ê∏∏ÊàèÈ¢òÁõÆ
const gameType = ref('identify')
const currentShape = ref({
  type: 'circle',
  radius: 0,
  side: 0,
  length: 0,
  width: 0,
  base: 0,
  height: 0,
  side1: 0,
  side2: 0,
  side3: 0
})
const userAnswer = ref('')
const showFeedback = ref(false)
const isCorrect = ref(false)
const feedbackMessage = ref('')
const showHint = ref(false)
const hintMessage = ref('')

// ÂÆöÊó∂Âô®
let gameTimer: NodeJS.Timeout | null = null
let feedbackTimer: NodeJS.Timeout | null = null

// ÈöæÂ∫¶ÈÖçÁΩÆ
const difficultyConfig = {
  easy: { time: 60, maxSize: 10 },
  medium: { time: 60, maxSize: 15 },
  hard: { time: 60, maxSize: 20 }
}

// ÂõæÂΩ¢ÈÄâÈ°π
const shapeOptions = ['ÂúÜÂΩ¢', 'Ê≠£ÊñπÂΩ¢', 'ÈïøÊñπÂΩ¢', '‰∏âËßíÂΩ¢']

// ËÆ°ÁÆóËøõÂ∫¶ÁôæÂàÜÊØî
const progressPercent = computed(() => {
  const config = difficultyConfig[selectedDifficulty.value as keyof typeof difficultyConfig]
  return ((config.time - timeLeft.value) / config.time) * 100
})

// ÁîüÊàêÊñ∞È¢òÁõÆ
const generateQuestion = () => {
  const config = difficultyConfig[selectedDifficulty.value as keyof typeof difficultyConfig]
  
  if (selectedDifficulty.value === 'easy') {
    // ÁÆÄÂçïÊ®°ÂºèÔºöÂõæÂΩ¢ËØÜÂà´
    gameType.value = 'identify'
    const shapes = ['circle', 'square', 'rectangle', 'triangle']
    const randomShape = shapes[Math.floor(Math.random() * shapes.length)]
    currentShape.value.type = randomShape
    hintMessage.value = '‰ªîÁªÜËßÇÂØüÂõæÂΩ¢ÁöÑÂΩ¢Áä∂ÁâπÂæÅ'
  } else if (selectedDifficulty.value === 'medium') {
    // ‰∏≠Á≠âÊ®°ÂºèÔºöÈù¢ÁßØËÆ°ÁÆó
    gameType.value = 'area'
    const shapes = ['circle', 'square', 'rectangle', 'triangle']
    const randomShape = shapes[Math.floor(Math.random() * shapes.length)]
    currentShape.value.type = randomShape
    
    if (randomShape === 'circle') {
      currentShape.value.radius = Math.floor(Math.random() * config.maxSize) + 1
      hintMessage.value = 'ÂúÜÁöÑÈù¢ÁßØ = œÄ √ó ÂçäÂæÑ¬≤ (œÄ ‚âà 3.14)'
    } else if (randomShape === 'square') {
      currentShape.value.side = Math.floor(Math.random() * config.maxSize) + 1
      hintMessage.value = 'Ê≠£ÊñπÂΩ¢ÁöÑÈù¢ÁßØ = ËæπÈïø¬≤'
    } else if (randomShape === 'rectangle') {
      currentShape.value.length = Math.floor(Math.random() * config.maxSize) + 1
      currentShape.value.width = Math.floor(Math.random() * config.maxSize) + 1
      hintMessage.value = 'ÈïøÊñπÂΩ¢ÁöÑÈù¢ÁßØ = Èïø √ó ÂÆΩ'
    } else if (randomShape === 'triangle') {
      currentShape.value.base = Math.floor(Math.random() * config.maxSize) + 1
      currentShape.value.height = Math.floor(Math.random() * config.maxSize) + 1
      hintMessage.value = '‰∏âËßíÂΩ¢ÁöÑÈù¢ÁßØ = Â∫ï √ó È´ò √∑ 2'
    }
  } else {
    // Âõ∞ÈöæÊ®°ÂºèÔºöÂë®ÈïøËÆ°ÁÆó
    gameType.value = 'perimeter'
    const shapes = ['circle', 'square', 'rectangle', 'triangle']
    const randomShape = shapes[Math.floor(Math.random() * shapes.length)]
    currentShape.value.type = randomShape
    
    if (randomShape === 'circle') {
      currentShape.value.radius = Math.floor(Math.random() * config.maxSize) + 1
      hintMessage.value = 'ÂúÜÁöÑÂë®Èïø = 2 √ó œÄ √ó ÂçäÂæÑ (œÄ ‚âà 3.14)'
    } else if (randomShape === 'square') {
      currentShape.value.side = Math.floor(Math.random() * config.maxSize) + 1
      hintMessage.value = 'Ê≠£ÊñπÂΩ¢ÁöÑÂë®Èïø = 4 √ó ËæπÈïø'
    } else if (randomShape === 'rectangle') {
      currentShape.value.length = Math.floor(Math.random() * config.maxSize) + 1
      currentShape.value.width = Math.floor(Math.random() * config.maxSize) + 1
      hintMessage.value = 'ÈïøÊñπÂΩ¢ÁöÑÂë®Èïø = 2 √ó (Èïø + ÂÆΩ)'
    } else if (randomShape === 'triangle') {
      currentShape.value.side1 = Math.floor(Math.random() * config.maxSize) + 1
      currentShape.value.side2 = Math.floor(Math.random() * config.maxSize) + 1
      currentShape.value.side3 = Math.floor(Math.random() * config.maxSize) + 1
      hintMessage.value = '‰∏âËßíÂΩ¢ÁöÑÂë®Èïø = ‰∏âËæπ‰πãÂíå'
    }
  }
  
  userAnswer.value = ''
  totalQuestions.value++
  
  // ÊòæÁ§∫ÊèêÁ§∫
  showHint.value = true
  setTimeout(() => {
    showHint.value = false
  }, 4000)
}

// Ê£ÄÊü•Á≠îÊ°à
const checkAnswer = () => {
  let isAnswerCorrect = false
  
  if (gameType.value === 'identify') {
    if (!userAnswer.value) return
    
    const shapeMap = {
      'ÂúÜÂΩ¢': 'circle',
      'Ê≠£ÊñπÂΩ¢': 'square',
      'ÈïøÊñπÂΩ¢': 'rectangle',
      '‰∏âËßíÂΩ¢': 'triangle'
    }
    
    isAnswerCorrect = shapeMap[userAnswer.value as keyof typeof shapeMap] === currentShape.value.type
  } else {
    // Á°Æ‰øù userAnswer ÊòØÊúâÊïàÁöÑÂ≠óÁ¨¶‰∏≤‰∏î‰∏ç‰∏∫Á©∫
    const answerStr = userAnswer.value?.toString()?.trim()
    if (!answerStr) return
    
    const userNum = parseFloat(answerStr)
    
    // Ê£ÄÊü•ÊòØÂê¶‰∏∫ÊúâÊïàÊï∞Â≠ó
    if (isNaN(userNum)) return
    
    const correctAnswer = calculateAnswer()
    
    // ÂÖÅËÆ∏‰∏ÄÂÆöÁöÑËØØÂ∑ÆËåÉÂõ¥
    isAnswerCorrect = Math.abs(userNum - correctAnswer) < 0.1
  }
  
  if (isAnswerCorrect) {
    isCorrect.value = true
    feedbackMessage.value = '‚úÖ Ê≠£Á°ÆÔºÅ'
    score.value += 10 * level.value
    correctAnswers.value++
    
    // ÊØèÁ≠îÂØπ5È¢òÂçá‰∏ÄÁ∫ß
    if (correctAnswers.value % 5 === 0) {
      level.value++
      feedbackMessage.value += ` ÂçáÁ∫ßÂà∞ ${level.value} Á∫ßÔºÅ`
    }
  } else {
    isCorrect.value = false
    if (gameType.value === 'identify') {
      const shapeNames = {
        'circle': 'ÂúÜÂΩ¢',
        'square': 'Ê≠£ÊñπÂΩ¢',
        'rectangle': 'ÈïøÊñπÂΩ¢',
        'triangle': '‰∏âËßíÂΩ¢'
      }
      feedbackMessage.value = `‚ùå ÈîôËØØÔºÅÊ≠£Á°ÆÁ≠îÊ°àÊòØ ${shapeNames[currentShape.value.type as keyof typeof shapeNames]}`
    } else {
      const correctAnswer = calculateAnswer()
      feedbackMessage.value = `‚ùå ÈîôËØØÔºÅÊ≠£Á°ÆÁ≠îÊ°àÊòØ ${correctAnswer.toFixed(2)}`
    }
    score.value = Math.max(0, score.value - 5)
  }
  
  showFeedback.value = true
  
  // Ê∏ÖÈô§ÂèçÈ¶àÊèêÁ§∫
  if (feedbackTimer) clearTimeout(feedbackTimer)
  feedbackTimer = setTimeout(() => {
    showFeedback.value = false
    generateQuestion()
  }, 2000)
}

// ËÆ°ÁÆóÊ≠£Á°ÆÁ≠îÊ°à
const calculateAnswer = () => {
  const shape = currentShape.value
  
  if (gameType.value === 'area') {
    if (shape.type === 'circle') {
      return Math.PI * shape.radius * shape.radius
    } else if (shape.type === 'square') {
      return shape.side * shape.side
    } else if (shape.type === 'rectangle') {
      return shape.length * shape.width
    } else if (shape.type === 'triangle') {
      return (shape.base * shape.height) / 2
    }
  } else if (gameType.value === 'perimeter') {
    if (shape.type === 'circle') {
      return 2 * Math.PI * shape.radius
    } else if (shape.type === 'square') {
      return 4 * shape.side
    } else if (shape.type === 'rectangle') {
      return 2 * (shape.length + shape.width)
    } else if (shape.type === 'triangle') {
      return shape.side1 + shape.side2 + shape.side3
    }
  }
  
  return 0
}

// ÂºÄÂßãÊ∏∏Êàè
const startGame = () => {
  gameStarted.value = true
  gameOver.value = false
  score.value = 0
  level.value = 1
  correctAnswers.value = 0
  totalQuestions.value = 0
  totalTime.value = 0
  
  const config = difficultyConfig[selectedDifficulty.value as keyof typeof difficultyConfig]
  timeLeft.value = config.time
  
  generateQuestion()
  startTimer()
}

// ÂºÄÂßãËÆ°Êó∂Âô®
const startTimer = () => {
  gameTimer = setInterval(() => {
    timeLeft.value--
    totalTime.value++
    
    if (timeLeft.value <= 0) {
      endGame()
    }
  }, 1000)
}

// ÁªìÊùüÊ∏∏Êàè
const endGame = () => {
  if (gameTimer) {
    clearInterval(gameTimer)
    gameTimer = null
  }
  gameOver.value = true
  
             // ‰øùÂ≠òÊ∏∏ÊàèËÆ∞ÂΩï
           gameStorage.addGameRecord({
             gameType: 'geometry',
             gameName: 'Âá†‰ΩïÊé¢Á¥¢',
             score: score.value,
             level: level.value,
             correctAnswers: correctAnswers.value,
             totalQuestions: totalQuestions.value,
             totalTime: totalTime.value,
             difficulty: selectedDifficulty.value
           })

           // Ê£ÄÊü•ÊàêÂ∞±
           achievementService.checkAchievements()
}

// ÈáçÊñ∞ÂºÄÂßãÊ∏∏Êàè
const restartGame = () => {
  gameStarted.value = false
  gameOver.value = false
  userAnswer.value = ''
  showFeedback.value = false
  showHint.value = false
}

// ËøîÂõûÊ∏∏ÊàèÂàóË°®
const goBack = () => {
  router.push('/games')
}

// ÁªÑ‰ª∂ÊåÇËΩΩÊó∂ËÅöÁÑ¶ËæìÂÖ•Ê°Ü
onMounted(() => {
  if (gameStarted.value && !gameOver.value) {
    setTimeout(() => {
      const input = document.querySelector('input') as HTMLInputElement
      if (input) input.focus()
    }, 100)
  }
})

// ÁªÑ‰ª∂Âç∏ËΩΩÊó∂Ê∏ÖÁêÜÂÆöÊó∂Âô®
onUnmounted(() => {
  if (gameTimer) clearInterval(gameTimer)
  if (feedbackTimer) clearTimeout(feedbackTimer)
})
</script>

